import { NextResponse } from 'next/server';

export async function GET() {
  try {
    const status = {
      application: 'FinBoard',
      version: '1.0.0',
      status: 'operational',
      timestamp: new Date().toISOString(),
      services: {
        api: {
          status: 'operational',
          endpoints: [
            { name: '/api/health', status: 'up' },
            { name: '/api/finance', status: 'up' },
          ]
        },
        database: {
          status: 'not_applicable',
          note: 'Using localStorage for persistence'
        },
        externalApis: {
          finnhub: {
            status: process.env.FINNHUB_API_KEY ? 'configured' : 'not_configured',
            rateLimit: '60 calls/minute (free tier)'
          }
        }
      },
      system: {
        nodeVersion: process.version,
        platform: process.platform,
        uptime: Math.floor(process.uptime()),
        memory: {
          used: Math.round(process.memoryUsage().heapUsed / 1024 / 1024),
          total: Math.round(process.memoryUsage().heapTotal / 1024 / 1024),
          unit: 'MB'
        }
      }
    };

    return NextResponse.json(status, { 
      status: 200,
      headers: {
        'Cache-Control': 'no-store, max-age=0'
      }
    });
  } catch (error: any) {
    return NextResponse.json(
      {
        application: 'FinBoard',
        status: 'error',
        timestamp: new Date().toISOString(),
        error: error.message
      },
      { status: 500 }
    );
  }
}
